.button{
    display: block;
    background-color: rgb(0, 0, 0);
    width: 40%;
    padding: 0.2rem;
    justify-self: center;
    margin:0 auto;
    border-radius: 0.5rem;
    margin-top: 0.5rem;
    border:1px rgba(103, 221, 103, 0.356) solid;
    align-items: center;
    justify-content: center;

    h1,h2,h3,h4,h5,h6,p{
        display: contents;
        font-family:Arial, Helvetica, sans-serif;
        color:rgb(195, 218, 195);
        transition:0.2s all;
    }

    a{
        font-weight: 200;
        text-decoration: none;
    }
}

.button:hover{
    cursor:pointer;

    h4{
        color:green;
        transition:0.2s all;
    }
}