.presentation{
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;

    h2,h3{
        color:rgb(195, 218, 195);
    }

    hr{
        border-color:green;
    }
}

.me-logo{
  width: 20vw;
  max-width: 18rem;
}

@media (max-width:800px) {
  .me-logo{
    width: 50vw;
  }
}

.principal-presentation{
    display: flex;
    justify-content: center;
    align-items: center;
    gap:30px;
}

.down-button{
    margin:6%;
}

.down-button:hover{
    cursor:pointer;
}

.down-button > a{
    color: white;
}

.text-box{
    width: 50%;
    text-align: center;
    max-width: 50%;
    display: table-column;
}

.container{
    display: flex;
    flex-wrap: wrap;
    width:100%;
    align-items: center;
    justify-content: center;
}

.download-button{
        position: relative;
        margin: 0;
        padding: 0 1em;
        outline: none;
        text-decoration: none;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
        border: none;
        text-transform: uppercase;
        background-color: #0f0e0e;
        border-radius: 10px;
        color: #fff;
        font-weight: 300;
        font-size: 18px;
        font-family: inherit;
        z-index: 0;
        overflow: hidden;
        transition: all 0.3s cubic-bezier(0.02, 0.01, 0.47, 1);
      }
      
      button:hover {
        animation: sh0 0.5s ease-in-out both;
      }
      
      @keyframes sh0 {
        0% {
          transform: rotate(0deg) translate3d(0, 0, 0);
        }
      
        25% {
          transform: rotate(7deg) translate3d(0, 0, 0);
        }
      
        50% {
          transform: rotate(-7deg) translate3d(0, 0, 0);
        }
      
        75% {
          transform: rotate(1deg) translate3d(0, 0, 0);
        }
      
        100% {
          transform: rotate(0deg) translate3d(0, 0, 0);
        }
      }
      
      button:hover span {
        animation: storm 0.7s ease-in-out both;
        animation-delay: 0.06s;
      }
      
      button::before,
      button::after {
        content: '';
        position: absolute;
        right: 0;
        bottom: 0;
        width: 100px;
        height: 100px;
        border-radius: 50%;
        background: #06d132;
        opacity: 0;
        transition: transform 0.15s cubic-bezier(0.02, 0.01, 0.47, 1), opacity 0.15s cubic-bezier(0.02, 0.01, 0.47, 1);
        z-index: -1;
        transform: translate(100%, -25%) translate3d(0, 0, 0);
      }
      
      button:hover::before,
      button:hover::after {
        opacity: 0.15;
        transition: transform 0.2s cubic-bezier(0.02, 0.01, 0.47, 1), opacity 0.2s cubic-bezier(0.02, 0.01, 0.47, 1);
      }
      
      button:hover::before {
        transform: translate3d(50%, 0, 0) scale(0.9);
      }
      
      button:hover::after {
        transform: translate(50%, 0) scale(1.1);
      }

.download-button:hover{
    cursor: pointer;
}